import React from 'react';
import ReorderRoundedIcon from '@mui/icons-material/ReorderRounded';
import IconButton from '@mui/material/IconButton';
import {
  sortableContainer,
  sortableElement,
  sortableHandle,
} from 'react-sortable-hoc';
import './sort.css';
import NormalizeView from './NormalizeView';

export const DragHandle = sortableHandle(() => (
  <IconButton className="dragHandler">
    <ReorderRoundedIcon />
  </IconButton>
));

export const SortableItem = sortableElement(
  ({ val, onDelete, onEdit, onGoto }) => (
    <div className="dragElement">
      <NormalizeView
        key={val.id}
        title={val.title}
        text={val.excerpt}
        onEdit={onEdit}
        onDelete={onDelete}
        onGoto={onGoto}
      />
      <DragHandle />
    </div>
  )
);

export const SortableContainer = sortableContainer(({ children }) => {
  return <div className="dragContainer">{children}</div>;
});

export const arrayMoveMutate = (array, from, to) => {
  array.splice(to < 0 ? array.length + to : to, 0, array.splice(from, 1)[0]);
};

export const arrayMove = (array, from, to) => {
  array = array.slice();
  arrayMoveMutate(array, from, to);
  return array;
};
