export default function QuillTestPage() {
  const { quill, quillRef } = useQuill({
    modules: {
      toolbar: {
        containers: '#toolbar',
        handlers: {
          link: (link) => {
            linkHandler(link);
          },
        },
      },
    },
  });

  const [html, setHtml] = useState('');

  function linkHandler(val) {
    console.log(val);
    if (quill) {
      const range = quill.getSelection();
      quill.insertEmbed(range.index, 'link', `http://localhost:9000${val}`);
    } else {
      console.log('quill null');
    }
  };

  useEffect(() => {
    if (quill) {
      // quill.register('modules/ImageResize', ImageResize);
      // quill.register('formats/link',Link);
      quill.insertText(0, 'Hello', 'link', 'https://world.com');
    }
  }, [quill]);
  const submitPost = (e) => {
    e.preventDefault();
    setHtml(quill.root.innerHTML);
  };

  return (
    <ReadContainer600>
      <InputHeading
        title="Rich Text"
        btnTxt="Test"
        submit={submitPost}
        back="/"
      />

      <div style={{ width: '100%', backgroundColor: 'white' }}>
        <div ref={quillRef} />
      </div>
      <br />
      <textarea value={html} onChange={(e) => setHtml(e.target.value)} />

      <br />

      <div dangerouslySetInnerHTML={{ __html: html }} />
      <br />
    </ReadContainer600>
  );
}
