import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router';
import SubmitLoading from './SubmitLoading';
import { ReadContainer600 } from './share';
import { SortableItem, SortableContainer, arrayMove } from './SortWrapper';
import { PROJECT_ROUTE, HOST_URL } from '../config';
import AddHeader from './AddHeader';

export default function SortableProjects() {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const navigate = useNavigate();
  const [editLoading, setIsLoading] = useState(true);

  const handleDelete = (id) => {
    setLoading(true);

    axios
      .delete(`${HOST_URL}/api/projects/${id}`)
      .then((res) => {
        fetchData();
      })
      .catch((err) => {
        alert('Sorry, Failed to delete!');
        console.log(err);
      })
      .finally(() => setLoading(false));
  };
  
  const fetchData = () => {
    axios
      .get(`${HOST_URL}/api/projects`)
      .then((res) => {
        setData(res.data);
      })
      .catch((err) => {
        console.log(err);
        setData([]);
      })
      .finally(() => {
        setLoading(false)
        setIsLoading(false)
      });
  };

  useEffect(() => {
    fetchData();
  }, []);

  const onSortEnd = ({ oldIndex, newIndex }) => {
    const update = arrayMove(data, oldIndex, newIndex);
    const map = update.map((val, i) => ({ id: val.id, position: i }));
    axios
      .put(`${HOST_URL}/api/projects/position`, map)
      .then((res) => {})
      .catch(({ response }) => {});
    setData(update);
  };

  const addToLatest = (m) => {
    const _data = {
      postId: m.id,
      url: m.url,
      name: m.name,
      title: m.title,
      price: m.price,
      image: m.media[0].thumbnail,
      position: data.length,
    };
    axios
      .post(`${HOST_URL}/api/projects`, _data)
      .then((res) => {
        setData((oldArray) => [
          ...oldArray,
          { ...m, image: m.media[0].thumbnail },
        ]);
      })
      .catch(({ response }) => {});
  };

  if (editLoading) return <div>Loading...</div>;

  return (
    <ReadContainer600>
      <AddHeader title="Projects" />
      <hr/>
      <SubmitLoading open={loading} />

      <SortableContainer onSortEnd={onSortEnd} useDragHandle>
        {data.map((val, index) => {
          return (
            <SortableItem
              key={val.id}
              index={index}
              val={val}
              onEdit={() => {
                navigate(`edit/${val.id}`);
              }}
              onDelete={() => {
                setData(data.filter((item) => item.id != val.id));
                handleDelete(val.id);
              }}
              onGoto={() => {
                window.open(`${HOST_URL}/${PROJECT_ROUTE}/${val.id}`, '_blank');
              }}
            />
          );
        })}
      </SortableContainer>
      <hr/>
      <button
        onClick={() => {
          setLoading(true);
          const data = {
            mainImage: {
              src: '/img/image.avif',
              small: '/img/image-small.avif',
              alt: 'some text',
            },
            slug: {
              current: 'example-post',
            },
            categories: [
              {
                slug: 'A-post',
              },
            ],
            title:
              'Architectural Engineering Wonders of the modern era for your Inspiration',
            body: '<p>The quick brown fox jumps over the lazy dog.</p>\n\n    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed euismod, velit vel bibendum bibendum, sapien sapien bibendum nunc, vel bibendum sapien sapien nec velit. Donec euismod, velit vel bibendum bibendum, sapien sapien bibendum nunc, vel bibendum sapien sapien nec velit.</p>\n    \n    <p>Here\'s some <strong>bold text</strong> and some <em>italic text</em>.</p>\n    \n    <p>Here\'s a list:</p>\n    <ul>\n    <li>Item 1</li>\n    <li>Item 2</li>\n    <li>Item 3</li>\n    </ul>\n    \n    <blockquote>\n    <p>The best way to predict the future is to invent it.</p>\n    </blockquote>\n    \n    <p>Here\'s a link: <a href="https://www.google.com/">Google</a></p>\n    \n    <p>Here\'s an image:</p>\n    <p><img src="https://picsum.photos/200/300" alt="alt text" /></p>\n    ',
            excerpt: 'This is an example post excerpt.',
            publishedAt: '2023-06-30T12:00:00Z',
            createdAt: '2023-06-29T10:30:00Z',
          };
          axios.post(`${HOST_URL}/api/projects`, data).then(fetchData);
        }}
      >
        Add One
      </button>
    </ReadContainer600>
  );
}
