import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import QuillTestPage from './views/QuillTestPage';
import ProjectPage from './views/ProjectPage';
import ProjectEditPage from './views/ProjectEditPage';

export default function App() {
  return (
    <BrowserRouter basename='/admin'>
      <Routes>
        <Route exact path="/quill" element={<QuillTestPage />}/>
        <Route exact path="/">
          <Route path="" element={<ProjectPage />} />
          <Route path="add" element={<ProjectEditPage />} />
          <Route path="edit/:id" element={<ProjectEditPage edit />} />
        </Route>
        <Route path="/*" element={<>404 Not found</>} />
      </Routes>
    </BrowserRouter>
  );
}
