import React, { useEffect, useState, useRef } from 'react';
import { useNavigate } from 'react-router';
import axios from 'axios';
import InputHeading from '../InputHeading';
import { BlogMedia, VirSpace3px, VirSpace6px } from '../share';
import { useParams } from 'react-router';
import { TextArea } from './components';
import { APP_URL, HOST_URL } from '../../config';
import { getUploadedImg } from '../../utils';
import { ReadContainer600 } from '../share';
import AdvancedEditor from './AdvancedEditor';
import './UploadProduct.css';

export default function Post({ edit }) {
  const { id } = useParams();
  const navigate = useNavigate();

  const [htmlContent, setHtmlContent] = useState(''); // empty is important here to print desc after api response
  const inputRef = useRef(null);
  const [data, setData] = useState({
    title: '',
    category: '',
    slug: { current: '' },
    excerpt: '',
    mainImage: {
      src: '',
      small: '',
      alt: '',
    },
  });
  const [uploadStatus, setUploadState] = useState({
    isUploading: false,
    uploadPercentage: 0,
    fileUploading: false,
    productUploading: false,
    progressbar: false,
  });
  const [imgUpload, setUploadedImg] = useState(null);
  const [smallSize, setSmallImg] = useState(null);
  const [loading, setLoading] = useState(false);
  const [editLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (edit) {
      setIsLoading(true);
      setLoading(true);
      axios
        .get(`${HOST_URL}/api/projects/${id}`)
        .then((res) => {
          const __data = res.data;
          __data.category = res.data.categories
            ?.map((cat) => cat.slug)
            .join(',');
          setData(res.data);
          setHtmlContent(res.data.body || ' ');
          setUploadedImg(res.data.mainImage.src);
          setSmallImg(res.data.mainImage.small);
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          setLoading(false);
          setIsLoading(false);
        });
    }
  }, [edit, id]);

  const submitPost = (e) => {
    e.preventDefault();

    setLoading(true);
    setUploadState({ ...uploadStatus, productUploading: true });
    
    let axiosIns = null;
    const _data = {
      ...data,
      body: inputRef.current?.value,
      mainImage: {
        alt: data.mainImage.alt,
        small: smallSize,
        src: imgUpload,
      },
      categories: data.category.split(',').map((slug) => {
        return { slug };
      }),
    };

    if (edit) {
      axiosIns = axios.put(`${HOST_URL}/api/projects`, _data);
    } else {
      axiosIns = axios.post(`${HOST_URL}/api/projects`, _data);
    }

    axiosIns
      .then((_) => {
        navigate('/');
      })
      .catch((err) => {
        setUploadState({ ...uploadStatus, productUploading: false });
        console.log(err);
      })
      .finally(() => setLoading(false));
  };

  const onDrop = (event) => {
    if (!event.target.files[0]) return;

    let formData = new FormData();
    formData.append('file', event.target.files[0]);

    const config = {
      header: { 'content-type': 'multipart/form-data' },
      onUploadProgress: (progressEvent) => {
        var percentCompleted = Math.round(
          (progressEvent.loaded * 100) / progressEvent.total
        );
        setUploadState({
          ...uploadStatus,
          uploadPercentage: percentCompleted,
          uploadCompleted: false,
          isUploading: true,
        });
      },
    };
    axios.post(`${HOST_URL}/api/photo`, formData, config).then((res) => {
      setUploadedImg(res.data.filePath);
      setSmallImg(res.data.small.replace('/uploads/small/', ''));
      setUploadState({
        ...uploadStatus,
        uploadPercentage: 0,
        uploadCompleted: true,
        isUploading: false,
      });
    });
  };

  if (editLoading) return <div>Loading...</div>;

  return (
    <ReadContainer600>
      <InputHeading
        title="Post"
        btnTxt="Submit"
        submit={submitPost}
        loading={loading}
        back="/"
      />
      <form onSubmit={submitPost} className="upload-moadal">
        <TextArea
          placeholder="Title"
          rows="1"
          onChange={(e) => {
            setData({
              ...data,
              title: e.currentTarget.value,
            });
          }}
          value={data.title}
        />
        <VirSpace3px />
        <TextArea
          placeholder="Slug"
          rows="1"
          onChange={(e) => {
            setData({
              ...data,
              slug: { current: e.currentTarget.value },
            });
          }}
          value={data.slug.current}
        />
        <BlogMedia>
          {imgUpload ? (
            <img
              className="previous-photo"
              src={getUploadedImg(imgUpload)}
              alt="project-view"
            ></img>
          ) : (
            <img
              className="placeholder"
              src={`${APP_URL}placeholder.png`}
              alt="placeholder"
            ></img>
          )}
          <p>Upload</p>
          <input
            className="update-photo"
            type="file"
            accept="image/*"
            onChange={(event) => onDrop(event)}
          />
        </BlogMedia>
        <VirSpace3px />
        <TextArea
          placeholder="Alt"
          rows="1"
          onChange={(e) => {
            setData({
              ...data,
              mainImage: { ...data.mainImage, alt: e.currentTarget.value },
            });
          }}
          value={data.mainImage?.alt}
        />
        <VirSpace3px />
        {edit && htmlContent && (
          <AdvancedEditor value={htmlContent} ref={inputRef} />
        )}
        {!edit && <AdvancedEditor value={htmlContent} ref={inputRef} />}
        <VirSpace3px />
        <TextArea
          placeholder="Excerpt"
          rows="1"
          onChange={(e) => {
            setData({
              ...data,
              excerpt: e.currentTarget.value,
            });
          }}
          value={data.excerpt}
        />
        <VirSpace3px />
        <TextArea
          placeholder="Categories"
          rows="2"
          onChange={(e) => {
            setData({
              ...data,
              category: e.currentTarget.value,
            });
          }}
          value={data.category}
        />
      </form>
    </ReadContainer600>
  );
}
